import Helper from "./helper.js";

//
function copyText(text) {
    wx.setClipboardData({
        data: text,
        success: function (res) {
            wx.hideToast()
            Helper.showSuccess('');
        },
        fail(err) {
            wx.hideToast()
            Helper.showError('');
        }
    })
}

function isEmpty(obj) {
    //nullundefined
    if (!obj && obj !== 0 && obj !== '') {
        return true;
    }
    //
    if (Array.prototype.isPrototypeOf(obj) && obj.length === 0) {
        return true;
    }
    //
    if (Object.prototype.isPrototypeOf(obj) && Object.keys(obj).length === 0) {
        return true;
    }
    return false;
}

function color16() {//
    let r = Math.floor(Math.random() * 256);
    let g = Math.floor(Math.random() * 256);
    let b = Math.floor(Math.random() * 256);
    let color = '#' + r.toString(16) + g.toString(16) + b.toString(16);
    return color;
}

//
function previewImage(src, current) {
    wx.previewImage({
        urls: src,
        current: src[current] || src[0],
        longPressActions: {
            itemList: ['', '', ''],
            success: function (data) {
                console.log('' + (data.tapIndex + 1) + ',' + (data.index + 1) + '');
            },
            fail: function (err) {
                console.log(err.errMsg);
            }
        }
    });
}

//
function filterPrice(num) {
    let arr = num.toString().split('.')
    if (arr.length == 1) {
        arr.push('00')
    } else if (arr[1].length == 1) {
        arr[2] += '0'
    }else if(arr[1].length>2){
        arr[1] = arr[1].slice(0,2)
    }
    return arr
}


function saveImage(src) {
    let saveImage = function (file) {
        wx.saveImageToPhotosAlbum({
            filePath: file,
            success(res) {
                console.log(res);
                Helper.showSuccess('')
            },
            fail(err) {
                console.log(err)
                if (err.errMsg === "saveImageToPhotosAlbum:fail:auth denied" || err.errMsg === "saveImageToPhotosAlbum:fail auth deny") {
                    wx.showModal({
                        title: '',
                        content: '',
                        showCancel: false,
                        success: modalSuccess => {
                            wx.openSetting({
                                success(settingdata) {
                                    console.log("settingdata", settingdata)
                                    if (settingdata.authSetting['scope.writePhotosAlbum']) {
                                        wx.showModal({
                                            title: '',
                                            content: ',',
                                            showCancel: false,
                                        })
                                    } else {
                                        wx.showModal({
                                            title: '',
                                            content: '~',
                                            showCancel: false,
                                        })
                                    }
                                },
                                fail(failData) {
                                    console.log("failData", failData)
                                },
                                complete(finishData) {
                                    console.log("finishData", finishData)
                                }
                            })
                        }
                    })
                } else {
                    Helper.showError('')
                }
            }
        })
    }
    if (src.indexOf("wxfile://") >= 0) {
        saveImage(src)
    } else {
        wx.downloadFile({
            url: src,
            success: function (res) {
                console.log(src);
                console.log(res.tempFilePath);
                saveImage(res.tempFilePath);
            },
            fail: (err) => {
                console.log(err)
            }
        })
    }
}


//id
function createShared(params, that) {
    createShare({type: params[0], target: params[1]}).then(res => {
        that.setData({
            share_id: res.info.data.id
        })
    })
}

//
function showNotify(data) {
    console.log(data)
    Notify({type: 'success', message: data.info.msg})
}

function getBz(num) {
    if (parseInt(num) < 10) {
        num = "0" + parseInt(num);
    }
    return num;
}

//
function changTimeFormat(time) {
    let oDate = new Date(time);
    let oYear = oDate.getFullYear();
    let oMonth = oDate.getMonth() + 1;
    let oDay = oDate.getDate();
    let setTime = oYear + "-" + getBz(oMonth) + "-" + getBz(oDay);//
    return setTime.toString()
}

//  
function timeFilter(str1='',str2='') {

    // console.log(str1,str2)

    let arr1 = str1.split(' ')
    let date1 = arr1[0].split('-')
    let time1 = arr1[1].split(':')

    if(!str2){
        return date1[1]+'/'+date1[2]+' '+time1[0]+':'+time1[1]
    }

    let arr2 = str2.split(' ')
    let date2 = arr2[0].split("-")
    let time2 = arr2[1].split(':')

    return date1[1]+'/'+date1[2]+' '+time1[0]+':'+time1[1]+'-'+time2[0]+':'+time2[1]

}

function computedTime(time) {

    let secondTime = parseInt(time)//Number
    let min = 0// 
    let h = 0// 
    let result = ''
    if (secondTime > 60) {//60
        min = parseInt(secondTime / 60)//60
        secondTime = parseInt(secondTime % 60)//
        if (min > 60) {//60
            h = parseInt(min / 60)//60
            min = parseInt(min % 60) //60
        }
    }
    result = `${h.toString().padStart(2, '0')}:${min.toString().padStart(2, '0')}:${secondTime.toString().padStart(2, '0')}`
    return result
}

function errMsg(res) {
    if(!res.status){
        return Helper.showError(res.msg)
    }
}

export {copyText ,isEmpty,errMsg, computedTime , timeFilter, previewImage, changTimeFormat, filterPrice, showNotify, color16, createShared, saveImage}