export default class Helper {
    /**
     * 
     * @param title
     * @param mask
     * @param timeout
     */
    static showLoading(title = '', mask = true, timeout = 5000) {
        wx.hideLoading()
        wx.showLoading({
            title: title,
            mask : mask
        })
        if (timeout) {
            setTimeout(wx.hideLoading, timeout)
        }
    }

    /**
     * 
     */
    static hideLoading() {
        wx.hideLoading()
    }

    static hideToast() {
        wx.hideToast()
    }

    /**
     * 
     * @param title
     * @param mask
     * @param duration
     */
    static showSuccess(title = '', mask = false, duration = 1500) {
        this.hideLoading()
        wx.showToast({
            title   : title,
            icon    : 'success',
            duration: duration,
            mask    : mask
        })
    }

    /**
     * 
     * @param title
     * @param mask
     * @param duration
     */
    static showError(title = '', mask = false, duration = 1500) {
        this.hideLoading()
        wx.showToast({
            title   : title,
            icon    : 'none',
            duration: duration,
            mask    : mask
        })
    }

    static reSizeImage(image, size) {
        if (typeof image !== "string") return image;
        if (image.indexOf("cdn.iyizhanke.com") >= 0) {
            if (image.indexOf("resize,w_") >= 0) {
                return image.replace(/resize,w_/i, 'resize,w_' + size)
            } else {
                return image + '?x-oss-process=image/resize,w_' + size;
            }
        } else {
            return image.replace(/_[0-9]+w_[0-9]+h_/i, '_' + size + 'w_' + size + 'h_')
        }
    }

    /**
     * 
     * @param goods 
     * @param size 
     * @returns {*[]}
     */
    static reSizeGoodsImage(goods = [], size = 300) {
        if (typeof goods != "object") return goods
        for (let key in goods) {
            if (key === 'goods_images') {
                for (let i = 0; i < goods[key].length; i++) {
                    goods[key][i] = this.reSizeImage(goods[key][i], size);
                }
            } else if (key === 'goods_image' || key === 'pictureUrl' || key === 'layout_image') {
                goods[key] = this.reSizeImage(goods[key], size);
            } else {
                this.reSizeGoodsImage(goods[key], size)
            }
        }
        return goods
    }

    static getScreenFactor() {
        if (!this.screenFactor) {
            const sysInfo     = wx.getSystemInfoSync();
            const screenWidth = sysInfo.screenWidth;
            this.screenFactor = screenWidth / 750; // 
        }
        return this.screenFactor
    }
    static throttle(fn,delay){
        var lastTime = 0;
        
        return function(){
            var nowTime = Date.now()
            if(nowTime - lastTime > delay){
                fn.call(this)
                lastTime = nowTime
            }
        }
        
    }
    static toPx(rpx) {
        return rpx * this.getScreenFactor();
    }

    static toRpx(px) {
        return px / this.getScreenFactor();
    }
}