/**
 * 
 * @param {Object} src 
 */
$.imagePreview = function(src,inx) {
	uni.previewImage({
		urls: src,
		longPressActions: {
			itemList: ['', '', ''],
			success: function(data) {
				console.log('' + (data.tapIndex + 1) + ',' + (data.index + 1) + '');
			},
			fail: function(err) {
				console.log(err.errMsg);
			}
		},
		current:inx || 0
	});
}

$.saveImage = function(src){
	let saveImage = function(file) {
		uni.saveImageToPhotosAlbum({
			filePath: file,
			success(res) {
				console.log(res);
				uni.showToast({
					title   : '',
					icon    : "success",
					duration: 1000
				})
			},
			fail(err) {
				console.log(err)
				if (err.errMsg === "saveImageToPhotosAlbum:fail:auth denied" || err.errMsg === "saveImageToPhotosAlbum:fail auth deny") {
					wx.showModal({
						title     : '',
						content   : '',
						showCancel: false,
						success   : modalSuccess => {
							wx.openSetting({
								success(settingdata) {
									console.log("settingdata", settingdata)
									if (settingdata.authSetting['scope.writePhotosAlbum']) {
										wx.showModal({
											title     : '',
											content   : ',',
											showCancel: false,
										})
									} else {
										wx.showModal({
											title     : '',
											content   : '~',
											showCancel: false,
										})
									}
								},
								fail(failData) {
									console.log("failData", failData)
								},
								complete(finishData) {
									console.log("finishData", finishData)
								}
							})
						}
					})
				} else {
					uni.showToast({
						title   : '',
						icon    : "none",
						duration: 1000
					})
				}
			}
		})
	}
	if(src.indexOf("wxfile://") >= 0){
		saveImage(src)
	}else{
		uni.downloadFile({
			url    : src,
			success: function(res) {
				console.log(src);
				console.log(res.tempFilePath);
				saveImage(res.tempFilePath);
			},
			fail   : (err) => {
				console.log(err)
			}
		})
	}
}
/**
 *   
 * @param {Object} value
 */
$.numFilter = function(value) {
	let realVal = ''
	if (value) {
		let tempVal = parseFloat(value).toFixed(3)
		realVal = tempVal.substring(0, tempVal.length - 1)
	}
	return realVal
}


// 4
$.passPhone = function(phone) {
	if(phone !== ''){
		var passPhone = phone.substr(0, 3) + "****" + phone.substr(7);
		return passPhone
	}
	return ''
}

/**
 * 
 * @param {Object} text
 */
$.copyless = function(text) {
	uni.setClipboardData({
		data: text
	});
};

// 
$.getSales = function(nums){
	if( nums >= 10000){
		return (nums / 10000).toFixed(1) + ''
	}
	return nums
}

export default $;
